@import '../../@theme/index';
$two_img: url('/assets/images/whoweare/image2.png');

.main {
    width: 1350px;
    margin: 0 auto;
    .text {
        width: 45%;
        vertical-align: top;
        height: 45vh;
        display: inline-block;
        h1 {
            font-size: 40px;
            text-align: left;
            font-family: $roboto;
            color: rgb(70, 70, 70);
            line-height: 1.3;
            margin: 100px 0 50px;
        }
        p {
            line-height: 35px;
            font-family: $helvetica;
            font-size: 25px;
            color: rgb(121, 111, 111);
            margin-left: 0;
        }       
          
       
    }
    .image {
        vertical-align: top;
        transform: rotate(9.3deg);
        width: 55%;
        height: 45vh;
        display: inline-block;
        background-image: $two_img;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
    }
    .thank {       
        font-size: 28px;
        font-family: $roboto;
        color: rgb(85, 85, 85);
        line-height: 3.964;
        text-align: center;       
                   
    }
    .company {
        margin-bottom: 100px;
        div {
            display: inline-block;
            width: 16.5%;
            height: 115px;
            margin: -2px 0;
            background-position: center;
            background-repeat: no-repeat;
            border-left: 1px solid #ddd;
            background-size: 50% auto;
           
              
            
        }
        :first-of-type
        {
            border-left: none;
        }
        :last-of-type {
            background-size: 30% auto;
        }
        :nth-child(7) {
            border-left: none;
        }
       
    }
}