@import '../../../../@theme/index';

.sponsorsBenefit {
  width: 33%;
  @media (max-width: $lg) {
    width: 95%;
  }
  &__wrap {
    width: 90%;
    padding: 0 20px;
    margin: auto;
    font-family: $roboto;
    @media (max-width: $sm-extra) {
      width: 100%;
      padding: 0;
    }
  }
  &__header {
    margin-bottom: 20px;
    position: relative;
    @media (max-width: $lg) {
      margin-bottom: 0px;
      align-items: flex-start;
    }
    @media (max-width: $sm) {
      width: 80%;
      height: auto;
      padding-left: 80px;
      margin-top: 20px;
    }
  }
  &__image {
    width: 70px;
    margin: 10px auto;
    display: block;
    height: 70px;
    padding: 13px 15px;
    background-color: #f7f7f7;
    border-radius: 50%;
    object-fit: contain;
    @media (max-width: $xxl) {
      width: 50px;
      height: 50px;
    }
    @media (max-width: $xl) {
      width: 40px;
      height: 40px;
    }

    @media (max-width: $sm) {
      width: 23px;
      padding: 11px;
      display: inline-block;
      background-color: #f7f7f7;
      border-radius: 50%;
      position: absolute;
      top: 0;
      margin: 0;
      left: 25px;
      height: 23px;
    }
    &__wrap {
      width: 105px;
      height: 105px;
      border-radius: 50%;
      background: $beASponsorGrayBg;
      margin: 0 auto;
      display: flex;
      justify-content: center;
      align-items: center;
      @media (max-width: $xxl) {
        width: 90px;
        height: 90px;
      }
      @media (max-width: $xl) {
        width: 75px;
        height: 75px;
      }
      @media (max-width: $xl) {
        width: 70px;
        height: 70px;
      }
      @media (max-width: $lg) {
        width: 70px;
        height: 70px;
      }
      @media (max-width: $sm-extra) {
        width: 60px;
        height: 60px;
      }
      @media (max-width: $sm-mini) {
        width: 40px;
        height: 40px;
      }
    }
  }
  &__title {    
    width: 100%;
    font: 500 18px/18px   $roboto;
    text-align: center;
    color: $headerSummText;
    margin: 25px auto;
    @media (max-width: $lg) {
      // width: 80%;
      margin: 15px 0;
    }
    @media (max-width: $sm) {
      font-weight: 500;
      margin: 10px 0;
      text-align: left;
      font-size: 14px;
      line-height: 20px;
    }
  }
  &__text {   
    font: 400 16px/25px  $roboto ;
    color: $footerText;    
    text-align: center;
    @media (max-width: $lg) {
      //margin-left: 20%;
      position: relative;
      // top: -45px;
    }
    @media (max-width: $sm-extra) {
      font-weight: normal;
      // top: -25px;
    }
    @media (max-width: $sm) {
      font-weight: normal;
      font-size: 12px;
      padding: 0;
      line-height: 16px;
      text-align: left;
      width: 250px;
     
      position: relative;
      // top: -15px;
    }
  }
}
